













































































import Vue from 'vue';
import {Component, Watch} from 'vue-property-decorator';
import NavBar from '@/components/common/NavBar.vue';
import Cover from '@/components/common/Cover.vue';
import {Toast} from 'vant';
import Comment from '@/components/comment/Comment.vue';

@Component({
  components: {Comment, Cover, NavBar}
})
export default class Article extends Vue {
  $http!: { get: (arg0: string) => any; post: (arg0: string, arg1: { comment_date: string; comment_content: string; parent_id: null; article_id: string }) => any }
    //categoryuserinfo


  //categoryuserinfo
  model = {category: {}, userinfo: {}};
  commendList = []; //

  //
  active = 0;
  userInfo = {};

  //
  myComment = '';
  /* eslint-disable */
  postcom = {
    comment_date: '',
    comment_content: '',
    parent_id: null,
    article_id: ''
  };

  //
  commentFetchFlag = false;

  likeFlag = false;
  collectFlag = false;

  //
  showShare = false;
  options = [
    {name: '', icon: 'wechat'},
    {name: '', icon: 'weibo'},
    {name: '', icon: 'link'},
    {name: '', icon: 'poster'},
    {name: '', icon: 'qrcode'},
  ];

  created() {
    this.articleData();
    this.commendData();
    this.userInfoData();
  }

  @Watch('$route.path')
  onRouteChanged() {
    this.articleData();
    this.commendData();
  }

  //
  async articleData() {
    const res = await this.$http.get('/web/api'+this.$route.path);
    this.model = res.data[0];
  }

  //
  async commendData() {
    const res = await this.$http.get('/web/api/commend');
    this.commendList = res.data;
  }

  //
  async userInfoData() {
    //token
    if (localStorage.getItem('id') && localStorage.getItem('objtoken')) {
      const res = await this.$http.get('/web/api/user/' + localStorage.getItem('id'));
      this.userInfo = res.data[0];
    }
  }

  async inputComment() {
    if (localStorage.getItem('id') && localStorage.getItem('objtoken')) {
      if (this.myComment.length <= 0) {
        Toast.fail('');
        return;
      }
      this.postcom.comment_date = new Date().toJSON().substring(5, 10);
      this.postcom.comment_content = this.myComment;
      this.postcom.parent_id = null;
      this.postcom.article_id = this.$route.params.id;
      const result = await this.$http.post('/web/api/comment_post/' + localStorage.getItem('id'), this.postcom);
      if (result.status == 200) {
        Toast.success('');
        this.myComment = '';
        this.commentFetchFlag = !this.commentFetchFlag;
      }
    } else {
      Toast.fail('');
    }
  }

  handleLike(){
    if(localStorage.getItem('id') && localStorage.getItem('objtoken')){
      this.likeFlag=!this.likeFlag
      Toast.success(this.likeFlag?'':'')
    }else{
      Toast.fail('')
    }
  }

  handleCollect(){
    if(localStorage.getItem('id') && localStorage.getItem('objtoken')){
      this.collectFlag=!this.collectFlag
      Toast.success(this.collectFlag?'':'')
    }else{
      Toast.fail('')
    }
  }

  //
  onSelect(option: { name: string | number | import("vant/types/toast").ToastOptions }) {
    Toast(option.name);
    this.showShare = false;
  }

}
