


















import Vue from 'vue';
import {Component, Watch} from 'vue-property-decorator';
import NavBar from '@/components/common/NavBar.vue';
import Cover from '@/components/common/Cover.vue';

@Component({
  components: {Cover, NavBar}
})
export default class Home extends Vue {
  $http!: { get: (arg0: string, arg1: { params: { page: any; pagesize: any } } | undefined) => any }

  active = 0;//
  category = []; //tabs

  userInfo ={}

  async fetchUserInfo() {
    if(localStorage.getItem('id') && localStorage.getItem('objtoken')){
      const res = await this.$http.get('/web/api/user/' + localStorage.getItem('id'),undefined);
      this.userInfo = res.data[0];
    }
  }

  mounted() {
    this.selectCategory();
    this.fetchUserInfo()
  }

  //tabs
  async selectCategory() {
    const res = await this.$http.get('/web/api/category',undefined);
    //tablist
    this.category = res.data.map((item: any) => {
      item.list = [];
      item.page=0; //
      item.pagesize =20; //20
      return item;
    });
    //  currentTab ,watch activethis.category
    await this.selectArticle()
  }

  //activetab
  @Watch('active') //immediate category, { immediate: true, deep: false }
  onActiveChanged() {
    this.selectArticle()
  }

  //currentTab
  async selectArticle() {
    const currentTab: any = this.category[this.active];
    const res = await this.$http.get('/web/api/detail/' + currentTab._id,{
      params:{
        page:currentTab.page,
        pagesize:currentTab.pagesize
      }
    });
    currentTab.list = res.data
  }

}
